import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from "@angular/core";
import { Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { DashboardSearchCriteria } from '../../dashboard/models/dash-search-criteria.model';
import { ActualDailyHours } from '../../resource/models/act-dailyhrs.model';
import { CLINPlotInfo } from '../../resource/models/clin-plotinfo.model';
import { ActualTimesheet } from "../models/act-sheet.model";
import { ActualHours } from '../models/acthours.model';
import { ActualHoursQuery } from '../models/acthr-query.model';
import { SendEmailQuery } from '../models/emailquery.model';
import { TDLPlanHoursQuery } from '../models/planhr-query.model';
import { VarianceHours } from '../models/varhours.model';
import { TaskAreaPlotInfo } from './../../resource/models/taskarea-plotinfo.model';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
};

@Injectable()
export class ActualSheetService {

  private tdlurl = environment.apiurl + '/api/v1/acthrs';

  constructor(private http: HttpClient) { }

  public saveActualHours(timesheet: ActualTimesheet, resourceID: number): Observable<ActualHours[]> {
    let actHours:ActualHours[]=[];
    for(let ptsr of timesheet.ptsprojects){
      let ah:ActualHours = new ActualHours();
      ah.weekEnding = timesheet.weekEnding;
      ah.actualHoursID = ptsr.actualHoursID;
      ah.tdlResourceID = ptsr.tdlResourceID;
      ah.resourceID = resourceID;
      ah.tdlID = ptsr.tdlID;
      ah.sunHrs = ptsr.hours[0];
      ah.monHrs = ptsr.hours[1];
      ah.tueHrs = ptsr.hours[2];
      ah.wedHrs = ptsr.hours[3];
      ah.thuHrs = ptsr.hours[4];
      ah.friHrs = ptsr.hours[5];
      ah.satHrs = ptsr.hours[6];
      ah.rate = ptsr.weekRate;
      actHours.push(ah);
    }
    return this.http.post(this.tdlurl, actHours, httpOptions).pipe(
			tap(_ => this.log("saveActualHours")),
			catchError(this.handleError<any>("saveActualHours"))
		);
  }

  public getActualResourceHoursForWeekEnding(query:ActualHoursQuery ):Observable<ActualHours[]>{
    return this.http.post(this.tdlurl + '/wkres', query, httpOptions).pipe(
			tap(_ => this.log("getActualResourceHoursForWeekEnding")),
			catchError(this.handleError<any>("getActualResourceHoursForWeekEnding"))
		);
  }

  public getResourceVarianceHoursForWeekEnding(query:ActualHoursQuery ):Observable<VarianceHours[]>{
    return this.http.post(this.tdlurl + '/wkresvar', query, httpOptions).pipe(
			tap(_ => this.log("getResourceVarianceHoursForWeekEnding")),
			catchError(this.handleError<any>("getResourceVarianceHoursForWeekEnding"))
		);
  }

  public getTotalActualHoursForTDL(tdlID: number ):Observable<any>{
    return this.http.get(this.tdlurl + '/tdl/hrs/' + tdlID, httpOptions).pipe(
			tap(_ => this.log("getTotalActualHoursForTDL")),
			catchError(this.handleError<any>("getTotalActualHoursForTDL"))
		);
  }


  /**
   *
   * @param query
   */
  public getCLINReport(query:TDLPlanHoursQuery):Observable<ActualDailyHours[]>{
    return this.http.post(this.tdlurl + '/clin', query, httpOptions).pipe(
			tap(_ => this.log("getCLINReport")),
			catchError(this.handleError<any>("getCLINReport"))
		);
  }

  public getCLINPlotInfo(dsc:DashboardSearchCriteria):Observable<CLINPlotInfo[]>{
    return this.http.post(this.tdlurl + '/clinplot', dsc, httpOptions).pipe(
			tap(_ => this.log("getCLINPlotInfo")),
			catchError(this.handleError<any>("getCLINPlotInfo"))
		);
  }

  public getTaskAreaPlotInfo(dsc:DashboardSearchCriteria):Observable<TaskAreaPlotInfo[]>{
    return this.http.post(this.tdlurl + '/taskareaplot', dsc, httpOptions).pipe(
			tap(_ => this.log("getTaskAreaPlotInfo")),
			catchError(this.handleError<any>("getTaskAreaPlotInfo"))
		);
  }

  // public ingestFile(formData: FormData): Promise<any> {
  //   return this.http.makeFileRequest("Post", this.tdlurl + '/ingestFile', formData);
  // }

  public sendTimeSheetEmail(query:SendEmailQuery):Observable<any>{
    return this.http.post(this.tdlurl + '/email/timesheet', query, httpOptions).pipe(
			tap(_ => this.log("sendTimeSheetEmail")),
			catchError(this.handleError<any>("sendTimeSheetEmail"))
		);
  }

  	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}
}
