/*
@author : Deloitte
Service class to perform tdlResource related operations.
*/

import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { TDLResource } from '../models/tdlresource.model';
import { ResourceTDLQuery } from '../../timesheet/models/restdl-query.model';
import { SwapTDLResource } from '../models/swaptdlresource.model';
import { TBDResource } from '../models/tbdresource.model';
import { tap, catchError } from 'rxjs/operators';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { environment } from '../../../environments/environment';

const httpOptions = {
	headers: new HttpHeaders({ "Content-Type": "application/json" })
}



/**
 *
 *
 * @export
 * @class TDLResourceService
 */
@Injectable()
export class TDLResourceService {

    private gettdlresourceurl = environment.apiurl + '/api/v1/tdlresource';

    constructor(private http: HttpClient) { }

    /**
     *
     *
     * @param {TDLResource} tdlResource
     * @returns {Observable<TDLResource>}
     *
     * @memberOf TDLResourceService
     */
    public createTDLResource(tdlResource: TDLResource): Observable<TDLResource> {
        return this.http.post(this.gettdlresourceurl, tdlResource, httpOptions).pipe(
			tap(_ => this.log("createTDLResource")),
			catchError(this.handleError<any>("createTDLResource"))
		);
    }

    /**
     */
    public createTBDTDLResource(tbdres: TBDResource): Observable<TDLResource> {
        return this.http.post(this.gettdlresourceurl + '/tbd', tbdres, httpOptions).pipe(
			tap(_ => this.log("createTBDTDLResource")),
			catchError(this.handleError<any>("createTBDTDLResource"))
		);
    }

    /**
     *
     * @param swapResource
     */
    public swapTDLResource(swapTDLResource: SwapTDLResource): Observable<TDLResource> {
        return this.http.post(this.gettdlresourceurl + '/swap', swapTDLResource, httpOptions).pipe(
			tap(_ => this.log("swapTDLResource")),
			catchError(this.handleError<any>("swapTDLResource"))
		);
    }

    /**
     *
     *
     * @param {TDLResource} tdlResource
     * @returns {Observable<TDLResource>}
     *
     * @memberOf TDLResourceService
     */
    public updateTDLResource(tdlResource: TDLResource): Observable<TDLResource> {
        return this.http.put(this.gettdlresourceurl, tdlResource, httpOptions).pipe(
			tap(_ => this.log("updateTDLResource")),
			catchError(this.handleError<any>("updateTDLResource"))
		);
    }

    /**
     *
     *
     * @param {any} id
     * @returns
     *
     * @memberOf TDLResourceService
     */
    public getTDLResource(id) {
        return this.http.get(this.gettdlresourceurl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("getTDLResource")),
			catchError(this.handleError<any>("getTDLResource"))
		);
    }

    public deleteTDLResourceById(id){
        return this.http.delete(this.gettdlresourceurl + '/' + id, httpOptions).pipe(
			tap(_ => this.log("deleteTDLResourceById")),
			catchError(this.handleError<any>("deleteTDLResourceById"))
		);
    }

    /**
     *
     *
     * @param {resourceId} resourceId
     * @returns
     *
     * @memberOf TDLResourceService
     */
    public getAll(){
        return this.http.get(this.gettdlresourceurl, httpOptions).pipe(
			tap(_ => this.log("getAll")),
			catchError(this.handleError<any>("getAll"))
		);
    }

    public getResourceTDLPlanViewForGivenWeekEnding(query:ResourceTDLQuery){
        return this.http.post(this.gettdlresourceurl + '/plan/tdl', query, httpOptions).pipe(
			tap(_ => this.log("getResourceTDLPlanViewForGivenWeekEnding")),
			catchError(this.handleError<any>("getResourceTDLPlanViewForGivenWeekEnding"))
		);
    }

    public getResourceTDLActualViewForGivenWeekEnding(query:ResourceTDLQuery){
        return this.http.post(this.gettdlresourceurl + '/actual/tdl', query, httpOptions).pipe(
			tap(_ => this.log("getResourceTDLActualViewForGivenWeekEnding")),
			catchError(this.handleError<any>("getResourceTDLActualViewForGivenWeekEnding"))
		);
    }

    public getResourceTDLPlanViewForGivenWeekEndingRange(query:ResourceTDLQuery){
        return this.http.post(this.gettdlresourceurl + '/plan/tdl/range', query, httpOptions).pipe(
			tap(_ => this.log("getResourceTDLPlanViewForGivenWeekEndingRange")),
			catchError(this.handleError<any>("getResourceTDLPlanViewForGivenWeekEndingRange"))
		);
    }

    public getResourceTDLActualViewForGivenWeekEndingRange(query:ResourceTDLQuery){
        return this.http.post(this.gettdlresourceurl + '/actual/tdl/range', query, httpOptions).pipe(
			tap(_ => this.log("getResourceTDLActualViewForGivenWeekEndingRange")),
			catchError(this.handleError<any>("getResourceTDLActualViewForGivenWeekEndingRange"))
		);
    }

    public getResourceTDLs(resourceId: number){
        return this.http.get(this.gettdlresourceurl + '/tdl/' + resourceId, httpOptions).pipe(
			tap(_ => this.log("getResourceTDLs")),
			catchError(this.handleError<any>("getResourceTDLs"))
		);
    }


    /**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
	private handleError<T>(operation = "operation", result?: T) {
		return (error: any): Observable<T> => {
			// TODO: send the error to remote logging infrastructure
			console.error(error); // log to console instead

			// TODO: better job of transforming error for user consumption
			this.log("${operation} failed: ${error.message}");

			// Let the app keep running by returning an empty result.
			return of(result as T);
		};
	}

	/** Log a Attribute Service message with the MessageService */
	private log(message: string) {
		//this.messageService.add("Profile Service: ${message}");
	}

}